.countIn {
    font-size: 2em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.countIn__beat {
    position: relative;
    -webkit-animation-name: introBeat;
            animation-name: introBeat;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
}

@-webkit-keyframes introBeat {
    0% {
        opacity: 0.75;
        top: 0;
    }
    50% {
        opacity: 1;
        top: 2em;
    }
    51% {
        opacity: 1;
        top: 2em;
        font-weight: bold;
    }
    100% {
        opacity: 0;
        top: 2em;
        font-weight: bold;
    }
}

@keyframes introBeat {
    0% {
        opacity: 0.75;
        top: 0;
    }
    50% {
        opacity: 1;
        top: 2em;
    }
    51% {
        opacity: 1;
        top: 2em;
        font-weight: bold;
    }
    100% {
        opacity: 0;
        top: 2em;
        font-weight: bold;
    }
}


